/* animation dropdown */
/* isOpen (false => true) */

.dropdown {
  overflow: hidden;
}

.dropdown__container {
  animation: translate-to-bottom 600ms ease-out;
}

@keyframes translate-to-bottom {
  0% {
    transform: translateY(-150px);
    margin-top: -100px;
    opacity: 0%;
  }
  100% {
    transform: translateY(0);
    margin-top: -20px;
    opacity: 100%;
  }
}

/* animation dropdown icon */

.dropdown__icon--revert {
  transform: rotate(180deg);
  transition: 500ms;
}

.dropdown__icon {
  transition: 200ms;
}
